const getCurrentPosition = (options: any) => {
    return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, options);
    })
}

const getCoordinates: () => Promise<Coordinates | null> = async () => {
    
    let coordinates = null;
    var options = {
        enableHighAccuracy: true,
        timeout: 1000,
        maximumAge: 0
    };

    try { 
        const crd:any = await getCurrentPosition(options);
        coordinates = {
            lat: crd.latitude,
            lng: crd.longitude,
            // utc: Date.now()
        } as Coordinates
    } catch (err) {
        console.warn(`ERROR(${err.code}): ${err.message}`);
    }


    return coordinates
}

interface Coordinates {
    lat: number;
    lng: number;
    utc?: number;
}

interface StartStopProps {
    isRunning: boolean,
    toggle: Dispatch<SetStateAction<boolean>>
}

const StartStop = (props: StartStopProps) => {
    const { isRunning, toggle } = props;
    return (
        isRunning 
            ? <a onClick={() => toggle(false)}>Pause</a>
            : <a onClick={() => toggle(true)}>Start</a>
    )
}
const Running = () => {
    const [coordinates, setCoordinates] = useState([] as Coordinates[]);
    const [isRunning, setIsRunning] = useState(true);

    async function updateCoordinates() {
        const currentPosition = await getCoordinates();
        console.log({coordinates});
        if(isRunning) {
            setInterval(() => {
                if(currentPosition !== null) {
                    setCoordinates([...coordinates, currentPosition])
                }
                //console.log(coordinates);
            }, 1000)
        } 
        
    }
    useEffect(() => {
        updateCoordinates();
    }, [])

    

    return (
        <Page>
            <h1>Run!</h1>
            <MileCounter />
            <Timer/>
            <StartStop isRunning={isRunning} toggle={setIsRunning}/>
            <Link href="/finished"><a>Stop</a></Link>
        </Page>
    )
}
    
export default Running